import React from "react"
import { StaticQuery, graphql } from "gatsby"

const Card = ({pokemon}) => {  

  const typesArray = `${pokemon.types}`;
  const typesToLower = typesArray.toLowerCase();
  const typesFormat = typesToLower.replace(/[, ]+/g, " ").trim();

  return (       

    <article key={pokemon.id} className={typesFormat} data-color={typesFormat}>
      <div className="title">
        <h3>{pokemon.name}</h3>
        <p><strong>{pokemon.maxHP}</strong>HP</p>        
      </div>
      
      <figure>        
        <img        
          src={pokemon.image}
          alt={pokemon.name}
          placeholder="blurred"          
          loading="lazy"
        />
      </figure>      

      <div className="data">
        <p>
          <strong>Weight:</strong> {pokemon.weight.maximum} - <strong>Height:</strong> {pokemon.height.maximum}
        </p>
      </div>

    </article>
  )
}

const PokeList = () => (
  <StaticQuery
    query={graphql`
    {
      pokemonsdata {
        pokemons(first: 151) {
          id
          name
          maxHP
          maxCP
          image
          resistant
          number
          weaknesses
          weight {
            maximum
            minimum
          }
          height {
            maximum
            minimum
          }
          types
          fleeRate
          attacks {
            special {
              damage
              name
              type
            }
            fast {
              damage
              name
              type
            }
          }
        }
      }
    }
  `}
    render={data => data.pokemonsdata.pokemons.map(pokemon => (
      <Card key={pokemon.id} pokemon={pokemon}></Card>
    ))}
  ></StaticQuery>
)

export default PokeList

